@import url(https://fonts.googleapis.com/css2?family=Exo+2&display=swap);
.App {
  text-align: center;
  margin-top: 9rem;
  font-family: 'Exo 2', sans-serif;
}



.formPage {
    border-radius: 2%;
    background: linear-gradient(rgb(223, 35, 91) 0%, rgb(153, 9, 158) 50%, rgb(174, 0, 255) 100%);
    margin: 0 25%;
    color: white;
}

.formHeader {
    padding-top: 2rem;
    font-size: 2rem;
    font-weight: bold;
    background: -webkit-radial-gradient(#eba31f, #eee);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.textInput {
padding: 0.5%;
width: 30vw;
margin-bottom: 1em;
margin-top: 1em;
}

.menu {
  padding: 0.5%;
  width: 31vw;
  margin: 1em 0;
}

.formButton {
    padding: 10px;
    margin: 1rem 0 3rem 0;
    text-align: center;
    font-weight: bold;
    font-size: 1em;
    width: 10vw;
    margin-top: 2%;
    border: white;
    border-radius: 2%;
    border-color: #3498db;
    color: #fff;
    box-shadow: 0 0 40px 40px #eba31f inset, 0 0 0 0 #252422;
    transition: all 150ms ease-in-out;
  }

.formButton:hover {
    box-shadow: 0 0 10px 0 rgb(223, 35, 91) inset, 0 0 10px 4px #eba31f;
    cursor: pointer;
    color:rgb(153, 9, 158);
}
.navigation {
    position: fixed;
    z-index: 10;
    background-color: black;
    width: 100%;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 7rem;
  }
  
.logo {
    width: 6rem;
    height: 6rem;
    padding-left: 2px;
  }
  .nav-link {
      text-decoration: none;
      font-weight: bold;
      color: white;
  }
.nav-link:hover {
color: rgb(223, 35, 91)
}
.nav-icon {
    font-size: 2rem;
}
.allFooter {
  background-color: rgb(0, 0, 0);
  color: #fff;
  padding-top: 2%;
}
.navigation-footer {
    position: static;
    margin-bottom: 0;
    margin-left: 0;
    width: 100%;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding-bottom: 2%;
  }
  
.logo-footer {
    width: 10rem;
    height: 10rem;
    padding-left: 2px;
  }

.contact {
display: -webkit-flex;
display: flex;
-webkit-flex-direction: column;
        flex-direction: column;
}

.displayCard {
    width: 60vw;
    height: 70vh;
    margin: 0 auto;
    border-radius: 2%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    overflow: auto;
    padding: 3rem;
    background: linear-gradient(rgb(223, 35, 91) 0%, rgb(153, 9, 158) 50%, rgb(174, 0, 255) 100%);
  }


.displayInfo {
    text-align: left;
    color: white;
}

.displayImg {
    max-width: 25 vw;
    max-height: 50 vw;
}

.homeDisplay {
    height: 40vh;
    margin-bottom: 2em;
}

.cardButton {
    padding: 10px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-weight: bold;
    font-size: 1em;
    width: 16rem;
    margin-top: 2%;
    border: white;
    border-radius: 2%;
    border-color: #3498db;
    color: #fff;
    box-shadow: 0 0 40px 40px #0c0c0c inset, 0 0 0 0 #252422;
    transition: all 150ms ease-in-out;
  }

.cardButton:hover {
    box-shadow: 0 0 10px 0 rgb(223, 35, 91) inset, 0 0 10px 4px #eba31f;
    cursor: pointer;
    color:rgb(153, 9, 158);
}

.buttonLink {
    text-decoration: none;
    color:#fff;
}

.buttonLink:hover {
    color:rgb(153, 9, 158);
}

.cardButtonSize{
    width: auto;
    height: auto;
}
.buttons{
    width: 10vw;
}

.commentArea {
    resize: none;
    overflow: auto;
    padding: 1%;
    background: rgb(214, 202, 202);
    color:rgb(223, 35, 91);
    border-radius: 1%;
    opacity: 0.5;
    font-size: 14px;
}

.commentContainer {
 display: -webkit-flex;
 display: flex;
 -webkit-justify-content: space-evenly;
         justify-content: space-evenly;
 margin-bottom: 2%;
 width: 70vw;
 margin-left: 18%;
 margin-right: 15%;
}

.commentSection {
    background: linear-gradient(rgb(223, 35, 91) 0%, rgb(153, 9, 158) 50%, rgb(174, 0, 255) 100%);
    width: 100vw;
    padding: 1% 0 5%;
    margin: 5% 0;
}

.userName {
    font-size: 1rem;
    color: #fff;
}
.bubble {
    font-size: 1.5rem;
}
